import i18n from 'i18next';
import Backend from 'i18next-xhr-backend';
import { initReactI18next } from 'react-i18next';

i18n
  .use(Backend)
  .use(initReactI18next)
  .init({
    lng: 'en',
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false,
    },
    backend: {
      loadPath: '/space_app/locales/{{lng}}/{{ns}}.json',
    },
    ns: ['translation'],
    react: {
      useSuspense: false,
    },
  });

export default i18n;
